.catalog {
  margin-top:  70px;
  width : 100%;
  display : flex;
  flex-direction : column;
  justify-content : center;
  align-items : center;
}

@media only screen and (min-width : 800px) {
   .catalog {
     width : 100%;
     display : flex;
     flex-direction : row;
     flex-wrap : wrap;
  }
}
